import React from 'react'
import './footer.css'
import { useMediaQuery } from '@mui/material'
import Typography from '@mui/material/Typography';
import { useTheme } from "@mui/material";
import Box from '@mui/material/Box';
import Tooltip from '@mui/material/Tooltip';
function Footer() {
  const theme=useTheme();
  const matches=useMediaQuery(theme.breakpoints.down('md'))

  return (
    <div className='footer'>
      <div className='generalInfo'>
      <Tooltip title='Alphabit@esi-sba.dz'>
<Box display="flex" alignItems="center" gap="6px">
  
  <i class="fa-regular fa-envelope"></i>
  
  
  <Typography sx={matches&&{
display:'none'
}} className='p'>: Alphabit@esi-sba.dz</Typography></Box>
</Tooltip>
  <Tooltip title='213542935481'>
<Box display="flex" alignItems="center" gap="6px">

  <i class="fa-solid fa-phone">
    </i>

  <Typography sx={matches&&{
display:'none'
}} className='p'>: 213542935481</Typography>
</Box>
</Tooltip>
      </div>
      <Box className='info' display='flex' alignItems='center' gap='10px' marginRight='10px'>
<Typography variant='p' className='time-working'>Open hours : 09:00am - 18:00pm : Sunday-Thursday </Typography>
<i className="fa-brands fa-facebook"></i>
<i className="fa-brands fa-instagram"></i>
<i className="fa-brands fa-discord"></i>
      </Box>
    </div>
  )
}

export default Footer
