import React, { useState } from "react";
import Header from "./Components/Header/Header";
import Footer from "./Components/Footer/Footer";
import Contact from "./Components/Contact/Contact";
import Events from "./Components/Events/Events";
import './App.css'
import Partner from './Components/Partner/Partner'

function App() {
  const [text, setText] = useState("");
  const [counter, setCounter] = useState(0);
  const [isDelete, setIsDelete] = useState(false);
  const alphaInfo = [
    "what's alphabit",
    "the first scientific club of Sidi Bel Abbes's national computer science",
    "our goals",
    "form students in both soft and hard skills",
  ];



  const switchSkills = () => {
    const textFormated = !isDelete
      ? alphaInfo[counter].substring(0, text.length + 1)
      : alphaInfo[counter].substring(0, text.length - 1);

    setText(textFormated);
    if (!isDelete && text === alphaInfo[counter]) {
      setIsDelete(true)
    } else if (isDelete && text === "") {
      setCounter((counter) => {
        if (counter === alphaInfo.length - 1) {
          return 0;
        } else {
          return counter + 1;
        }
      });
      setIsDelete(false);
    }
  }



  const [value, setValue] = useState(0);
  const [eventsTitle, setEventsTitle] = useState([]);
  const getBadgevalue = (value) => {
    setValue(value.length);
    setEventsTitle(value.map((item) => item.name));
  };
  return (

   
 
      <div
      className="App"
       
      >
        <Footer />
        <Header badge={value} titles={eventsTitle} textshown={text}/>
        <Events getbadge={getBadgevalue} switchSkills={switchSkills} />
        <Partner />
        <Contact />
      </div>
 
    
  );
}

export default App;
