import React, { useState } from "react";
import { useEffect } from "react";
import Stack from "@mui/material/Stack";
import Badge from "@mui/material/Badge";
import Tooltip from "@mui/material/Tooltip";
import "./Header.css";
import { Box } from "@mui/material";
import links from "../../Data/Data";
import useMediaQuery from "@mui/material/useMediaQuery";
import { useTheme } from "@mui/material";
export default function Navbar({ badge, titles ,textshown}) {
  const [isShown, setIsShown] = useState(false);
  const theme = useTheme();
  const matches = useMediaQuery(theme.breakpoints.down("md"));
  const element = document.querySelector(".Box");
 console.log(textshown)
  return (
    <div className="header">
      <div className="blur blur1"></div>
      <div className="blur blur2"></div>
      <Stack
        direction="row"
        className="navbar"
        sx={{
          height: "80px",

          display: "flex",
          alignItems: "center",
          width: "100%",
          justifyContent: "space-around",
          boxShadow: "0 .5rem 1rem rgba(0,0,0,.1)",
        }}
      >
        <div
          className="HandleBtn"
          style={
            matches
              ? {
                  display: "block",
                }
              : { display: "none" }
          }
          onClick={() => {
            setIsShown(!isShown);
            if (element.classList.contains("opened")) {
              element.classList.remove("opened");
            } else {
              element.classList.add("opened");
            }
          }}
        >
          {isShown ? (
            <i class="fa-solid fa-angle-up"></i>
          ) : (
            <i class="fa-solid fa-angle-down"></i>
          )}
        </div>
        <Tooltip title="alphaBit">
          <span className="nav-brand"></span>
        </Tooltip>

        <Box
          width="200px"
          sx={{
            display: "flex",
            alignItems: "center",
            height: "40px",
            borderRadius: "10px",
            backgroundColor: "white",
            padding: "10px",
          }}
        >
          <i
            className="fa-solid fa-magnifying-glass"
            styles={{
              flexDirection: "start",
              color: "rgb(3, 89, 160)",
            }}
          ></i>
          <input
            className="text-control"
            type="text"
            style={{
              height: "80%",
              width: "90%",
              position: "relative",
              border: "none",
              padding: "10px",
              outline: "none",
            }}
          ></input>
        </Box>
        <Box
          sx={
            matches
              ? {
                  position: "absolute",
                  top: "0px",
                  zIndex: "-10",
                  width: "300px",
                  height: "40px",
                  padding: "20px",
                  display: "flex",
                  alignItems: "center",
                  transition: "all .6s ease-in-out",
                }
              : !matches && {
                  display: "flex",
                  justifyContent: "center",
                  gap: "10px",
                  transition: "all .6s ease-in-out",
                }
          }
          className="Box"
        >
          {links.map((item) => (
            <a href={item.link} className="nav-link" key={item.id}>
              {item.name}
            </a>
          ))}
        </Box>
        <Tooltip title={titles}>
          <Badge badgeContent={badge} color="primary">
            <i className="fa-solid fa-bell"></i>
          </Badge>
        </Tooltip>
      </Stack>
      <Box margin="auto" sx={{
         display: 'flex',
         justifyContent: 'center',
         height: '80%',        
          alignItems: 'center',
         width: '100%'
      }} id="home">
        <Box className="main-photo" display={matches && "none"}></Box>
        <Box className="main-content" width={matches ? "100%" : "50%"}>
          {matches && (
            <h1 className="alphaTitle">
              Alpha<span>Bit</span>Club
            </h1>
          )}

          <Box
            width={matches && "80%"}
            justifyContent={matches && "space-between"}
            alignItems="center"
            display={matches ? "flex" : "block"}
            gap={matches ? "60px" : "20px"}
            flexDirection={matches && "row"}
            sx={{
              margin: "0px auto",
            }}
          >
            <h1 style={{ width: "300px" }}>
              <span>Time To Raise The </span>
              Bar Higher
            </h1>
            <div
              style={{
                width: "300px",
              }}
            >
              <h6>{textshown}</h6>
              <a href="www.google.com" className="join">
                join <i className="fa-solid fa-circle-right"></i>
              </a>
            </div>
          </Box>
        </Box>
      </Box>
    </div>
  );
}
