import React, { useEffect, useState } from "react";
import { Box, Stack } from "@mui/material";
import ctf from "../../img/ctf.JPG";
import { motion } from "framer-motion";
import hash from "../../img/hash.JPG";
import hackathon from "../../img/hackathon.JPG";
import startUp from "../../img/startUp.JPG";
import useMediaQuery from "@mui/material/useMediaQuery";
import { useTheme } from "@mui/material";
import "./Events.css";
function Events({ getbadge,switchSkills }) {
  const [index, setIndex] = useState(3);
  const [favoritesEvent, setFavoritesEvent] = useState([]);
  const theme = useTheme();
  const matches = useMediaQuery(theme.breakpoints.down("md"));


  const [events,setEvents] =useState([
    {
      name: "Hackathon",
      description:
        "Lorem ipsum dolor sit amet consectetur adipisicing elit. Ducimus ullam omnis repellat sint aspernatur eum illo maiores suscipit iste qui ad, id ipsam non, rem excepturi. Aliquam adipisci ipsum dolor?",
      img: hackathon,
      id: "1",
      like: false
    },
    {
      name: "alphaCTF",
      description:
        "Lorem ipsum dolor sit amet consectetur adipisicing elit. Ducimus ullam omnis repellat sint aspernatur eum illo maiores suscipit iste qui ad, id ipsam non, rem excepturi. Aliquam adipisci ipsum dolor?",
      img: ctf,
      id: "2",
      like: false
    },
    {
      name: "HashCode",
      description:
        "Lorem ipsum dolor sit amet consectetur adipisicing elit. Ducimus ullam omnis repellat sint aspernatur eum illo maiores suscipit iste qui ad, id ipsam non, rem excepturi. Aliquam adipisci ipsum dolor?",
      img: hash,
      id: "3",
      like: false
    },
    {
      name: "StartUp Weekend",
      description:
        "Lorem ipsum dolor sit amet consectetur adipisicing elit. Ducimus ullam omnis repellat sint aspernatur eum illo maiores suscipit iste qui ad, id ipsam non, rem excepturi. Aliquam adipisci ipsum dolor?",
      img: startUp,
      id: "4",
      like: false
    },
  ]);
  const [event, setEvent] = useState([events[0]]);

 
  useEffect(() => {
    setEvent(events.filter((item) => events.indexOf(item) === index))
    getbadge(favoritesEvent)
   
  }, [events,favoritesEvent, getbadge]);

useEffect(()=>{
const Interval=setInterval(
  switchSkills()
,1000)
return ()=>clearInterval(Interval)}
)
  return (
  
      <Stack direction="column" className="Event" id="events">
      <h1>Our Events</h1>
      <Box
        width="100%"
        sx={{
          margin: "30px auto",
          padding: "20px",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <motion.div animate={
          {
            y:-50
        }
        }
        initial={{
          y:20
        }}
        transition={{delay:3}}>

        <Box className="Event-png" display={matches && 'none'}></Box>
        </motion.div>
        

        <div className="Event-content">
          <i
            className="fa-solid fa-angle-left"
            onClick={() => {
              if (index === 0) {
                setIndex(events.length-1);
                console.log(events.length);
              } else setIndex((i) => i - 1);
            }}
          ></i>

          {event.map((item) => 
            <div className="content" key={item.id}>
              <div
                style={{
                  backgroundImage: `url(${item.img})`,
                  height: "250px",
                  width: "250px",
                  backgroundSize: "cover",
                  marginBottom: "30px",
                  borderRadius: "50%",
                }}
              ></div>
              <h3>{item.name}</h3>
              <div className="lignTitle"></div>
              <div
                className="likeBtn"
                onClick={() => {
                  item.like = !item.like;
                  if (item.like) {
                    setFavoritesEvent([...favoritesEvent, item]);
                    console.log(favoritesEvent);
                  } else {
                    favoritesEvent.length !== 0 &&
                      setFavoritesEvent(
                        favoritesEvent.filter((el) => el.id !== item.id)
                      );
                  }
                }}
              >
                {item.like ? (
                  <i className="fa-solid fa-heart" style={{ color: "red" }}></i>
                ) : (
                  <i className="fa-regular fa-heart"></i>
                )}
              </div>
            </div>
          )}

          <i
            className="fa-solid fa-angle-right"
            onClick={() => {
              if (index === events.length - 1) {
                setIndex(0);
              } else setIndex((i) => i + 1);
            }}
          ></i>
        </div>
      </Box>
    </Stack>
  
    
  );
}

export default Events;
